import findWebApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        webCode: 0,
        webData: {
            
        }
    },
    mutations: {
        findWeb(state, payload) {//state, payload   
            Object.assign(state,{
                webData:payload.data,
                webCode:payload.data
            })//data5state   

        }
    },
    actions: {
        async findWebServerAsync(context, payload) {  //state, payload
            
            const data = await findWebApi.findWebServer({serverId:payload})//data18data13payload
            context.commit("findWeb", data)//12findWeb

        },
    }
}