import findFlowApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        flowCode: 0,
        flowData: {
            
        }
    },
    mutations: {
        findFlow(state, payload) {//state, payload   
            Object.assign(state,{
                flowData:payload.data,
                flowCode:payload.data
            })//data5state   
            // console.log(payload);
            
        }
    },
    actions: {
        async findFlowAsync(context, payload) {  //state, payload
            
            const data = await findFlowApi.findFlowSetting({serverId:payload})//data18data13payload
            context.commit("findFlow", data)//12findWeb

        },
    }
}