import findNodeApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        code: 0,
        data: { }
    },
    mutations: {
        findNode(state, payload) {//state, payload            
            Object.assign(state, payload,payload.data)//data5state
        }
    },
    actions: {
        async findNodeServerAsync(context, payload) {  //context, payload
            const data = await findNodeApi.findNodeServer({serverId:payload})//data18data13payload
            context.commit("findNode", data)//12findNode         
        },
    }
}