import findAttackApi from "../services/serListServices";

export default {
    namespaced: true,
    state: {
        attCode: 0,
        attData: {
            
        }
    },
    mutations: {
        findAttack(state, payload) {//state, payload   
            Object.assign(state,{
                attCode:payload.code,
                attData:payload.data
            })//data5state   
            // console.log(payload);
        }
    },
    actions: {
        async findAttackServerAsync(context, payload) {  //state, payload
            const data = await findAttackApi.findAttackS({serverId:payload})//data18data13payload
            context.commit("findAttack", data)//12findWeb
        },
    }
}