<template>
  <div>
    <el-table :data="list">
      <el-table-column prop="id" label="ID"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="connStatus" label="" :formatter="changeConnStatus"></el-table-column>
      <el-table-column prop="currentsupport" label="" :formatter="changeCurrentsupport"></el-table-column>
      <el-table-column prop="runningState" label="" :formatter="changeRunningState"></el-table-column>
      <el-table-column prop="system" label=""></el-table-column>
      <el-table-column prop="host" label="IP"></el-table-column>
      <el-table-column prop="local" label=""></el-table-column>
      <el-table-column prop="serverType" label="" :formatter="changeServerType"></el-table-column>
      <el-table-column prop="attackManagerStatus" label="" :formatter="changeAttack"></el-table-column>
      <el-table-column fixed="right" label="" width="120">
        <template slot-scope="scope">
          <el-button @click="detali(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="total"
      :page-size="pageSize"
      :current-page="pageNum"
      @current-change="handleCurrentChange"
    ></el-pagination>
  </div>
</template>

<style scoped>
.el-pagination {
  text-align: center;
  margin-top: 15px;
}
</style>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapActions, mapState } = createNamespacedHelpers("serList");
export default {
  mounted() {
    this.serchListAsync();
  },
  data() {
    return {};
  },
  computed: {
    ...mapState(["pageNum", "pageSize", "list", "total"])
  },
  methods: {
    ...mapActions(["serchListAsync", "changePageAsync"]),
    detali(prop) {
      sessionStorage.setItem("webID", JSON.stringify(prop));
      location.hash = "#/Info/serveDetails";
      // this.$router.push({
      //   path: "/Info/serveDetails",
      //   query: {
      //     id: prop.id,
      //     name: prop.name,
      //     runningState: prop.runningState
      //   }
      // });
    },
    handleCurrentChange(val) {
      this.changePageAsync(val);
    },
    changeConnStatus(row, column) {
      switch (row.connStatus) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
      }
    },
    changeCurrentsupport(row, column) {
      switch (row.currentsupport) {
        case "0":
          return "TOR";
          break;
        case "1":
          return "12P";
          break;
        case "2":
          return "ZeroNet";
          break;
      }
    },
    changeRunningState(row, column) {
      switch (row.runningState) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
      }
    },
    changeServerType(row, column) {
      switch (row.serverType) {
        case 0:
          return "";
          break;
        case 1:
          return "blog";
          break;
        case 2:
          return "";
          break;
      }
    },
    changeAttack(row, column) {
      switch (row.attackManagerStatus) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
      }
    },
  }
};
</script>