import axios from "axios";
import qs from 'qs';

export default {
    //
    async nodeList(params) {
        let {
            data
        } = await axios.post("/aw/nodeInfo/findNodeInfoManager", qs.stringify(params))
        return data;
    },
    //
    async nodeDetails(params) {
        let {
            data
        } = await axios.post("/aw/nodeInfo/findNodeDetail", qs.stringify(params))
        return data;
    },
}